// BrowserID.js
import FingerprintJS from "@fingerprintjs/fingerprintjs";

class BrowserID {
  constructor() {
    this.fpPromise = null;
    this.init();
  }

  //  FingerprintJS
  init() {
    try {
      this.fpPromise = FingerprintJS.load();
      return true;
    } catch (error) {
      console.error(":", error);
      return false;
    }
  }

  // IDID + ID
  getCombinedId(successCallback, errorCallback) {
    // 
    if (!this.fpPromise) {
      if (!this.init()) {
        errorCallback("");
        return;
      }
    }

    // ID
    this.fpPromise
      .then((fp) => fp.get())
      .then((result) => {
        const deviceId = result.visitorId;

        // ID
        const timestamp = new Date().getTime();
        const dateStr = new Date(timestamp).toISOString().replace(/[-:T]/g, "").slice(0, 14);
        const combinedId = `${deviceId}_T${dateStr}`;

        // 
        successCallback({
          deviceId,
          combinedId,
          timestamp,
        });
      })
      .catch((error) => {
        errorCallback("ID: " + error.message);
      });
  }
}

//  BrowserID 
const browserID = new BrowserID();

// API
export default {
  /**
   * IDIDID
   * @param {function} callback -  { success: boolean, deviceId?: string, combinedId?: string, timestamp?: number, error?: string }
   */
  getCombinedId: function (callback) {
    browserID.getCombinedId(
      (result) =>
        callback({
          success: true,
          deviceId: result.deviceId,
          combinedId: result.combinedId,
          timestamp: result.timestamp,
        }),
      (error) =>
        callback({
          success: false,
          error,
        })
    );
  },
};
