<template>
  <!-- <button @click="$store.commit('guan')"> x</button> -->
  <div id=" login" class="content-page" v-show="$store.state.isdenglushow">
    <div style="
        max-width: 450px; 
        margin: 0 auto;
        background: white;
        border-radius: 16px;
        box-shadow: var(--card-shadow);
        overflow: hidden;
        width: 395px;
      ">
      <div style="background: var(--primary-color); color: white; text-align: center; padding: 30px">
        <h2 style="font-size: 28px; margin-bottom: 10px"></h2>
        <p></p>
      </div>
      <div style="padding: 30px">
        <div class="form-group">
          <label for="login-username"></label>
          <input type="text" id="login-username" class="form-control" placeholder="" name="email" v-model="email"
            @input="jiancename" />
          <span class="yonghumingcuowu">{{ yonghuemail }}</span>
        </div>
        <div class="form-group">
          <label for="login-password"></label>
          <input type="password" id="login-password" class="form-control" placeholder="" name="Password"
            v-model="password" @input="jiancepassword" />
          <span class="yonghumingcuowu">{{ yonghupassword }}</span>
        </div>
        <div style="
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;
          ">
          <div>
            <input type="checkbox" id="remember" style="cursor: pointer;" v-model="tongyi" @click="iftongyi" />
            <label style="margin-left: 5px ;color: var(--primary-color);cursor: pointer;" @click="xieyi"></label>
          </div>
          <a href="#" style="color: var(--primary-color); text-decoration: none" @click="wnagijpassdorw">?</a>
        </div>

        <button :disabled="isDisabled" class="btn btn-primary btn-block" id="loginBtn" type="submit" @click="denglu">
          {{ annuvalue }}
        </button>

        <div style="
            display: flex;
           justify-content: center;
            align-items: center;
            margin-top: 20px;
          ">
        </div>
        <div style="
            text-align: center;
            margin-top: 25px;
            padding-top: 20px;
            border-top: 1px solid var(--border-color);
          ">
          <p>
            ?
            <a id="goToRegister" style=" color: var(--primary-color); text-decoration: none; font-weight: 500"
              @click="lijizhuce"></a>
          </p>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
// import api from "@/store/axiosqingqiu";
// import { mapState } from "vuex";
import loadXMLDoc from '@/store/AjaxRequest';
import BrowserID from '@/store/BrowserID';

export default {
  data() {
    return {
      //name
      email: "",
      yonghuemail: "",

      //password
      password: "",
      yonghupassword: "",

      //
      ifjizhume: false,

      //
      tongyi: false,

      //
      userarr: "",

      //
      dengluname: '',
      denglupassword: "",

      //
      isDisabled: false,
      annuvalue: "",

      // ID
      DeviceID: "",

    };
  },
  // created() {

  // },

  methods: {
    //
    denglu() {
      if ((this.email).trim() == "" || (this.password).trim() == "") {
        this.$store.commit("tanchaung", "")
        this.$store.commit("tanchuangshow", true)
        return
      }

      this.isDisabled = true;
      this.annuvalue = "...";

      if (this.tongyi) {
        let tokenFetchFailed = false;
        //token
        loadXMLDoc({
          reqBody: { 'email': this.email, 'password': this.password, 'deviceid': this.DeviceID },
          // reqBody: { 'email': "hm2897135735@qq.com", 'password': "88888888", 'deviceid': this.DeviceID },
          reqUrl: 'http://8.148.31.153:3030/login',
          async: false,
          callback: (error, resData) => {
            try {
              const data = JSON.parse(resData)
              if (!error) {
                // token
                if (data.success) {
                  localStorage.setItem("token", null)
                  localStorage.setItem('token', data.resData.token);
                } else {
                  // token
                  tokenFetchFailed = true;
                  this.$store.commit("tanchaung", data.message.msg)
                  this.$store.commit("tanchuangshow", true)
                  this.annuvalue = "";
                  this.isDisabled = false;
                }
              }
            } catch (error) {
              console.log(error)
            }
          }
        })
        if (tokenFetchFailed) {
          return;
        }

        const value = localStorage.getItem("token")
        if (value == "null" || value == "undefined" || value == '') {
          this.$store.commit("tanchaung", "")
          this.$store.commit("tanchuangshow", true)
          this.annuvalue = "";
          this.isDisabled = false;
          return
        }
        //
        loadXMLDoc({
          reqBody: {
            'token': localStorage.getItem("token"), 'deviceid': localStorage.getItem("equipment")
          },
          reqUrl: 'http://8.148.31.153:3030/getInfos',
          async: false,
          callback: (error, resData) => {
            try {
              const data = JSON.parse(resData)
              if (!error) {
                if (data.success) {
                  //
                  localStorage.setItem("denglutoken", true)
                  //
                  this.$store.commit("dlcg", "")
                  //
                  localStorage.setItem("resData", resData)
                  this.$store.commit("yonghuxx", resData)
                  // const times1 = setTimeout(() => { this.$router.push({ name: "FunctiontWorkbench" }), this.$store.commit("ifisgongzuotaishow", true) }, 2000)
                  this.$router.push({ name: "FunctiontWorkbench" })
                  this.$store.commit("ifisgongzuotaishow", true)
                  this.tokenOk = false
                  this.isDisabled = false
                } else {
                  this.$store.commit("tanchaung", data.message.msg == "" ? "" : data.message.msg)
                  this.$store.commit("tanchuangshow", true)
                  this.annuvalue = ""
                  this.isDisabled = false
                }
              }
            } catch (error) {
              console.log(error.message)
            }
          }
        })
      } else {
        //   
        this.annuvalue = ""
        this.$store.commit("tanchaung", "")
        this.$store.commit("tanchuangshow", true)
        this.isDisabled = false
      }
    },

    //
    iftongyi() {
      this.tongyi = !this.tongyi
      BrowserID.getCombinedId(response => {
        if (response.success) {
          this.DeviceID = response.combinedId;
          localStorage.setItem("equipment", this.DeviceID)
        } else {
          this.error = response.error;
          console.log(this.error)
        }
      });
    },

    //
    jiancename() {
      //name
      if (!/^[a-zA-Z0-9\u4e00-\u9fa5._-]+@[a-zA-Z0-9.-]+\.[a-zA-Z]{2,}$/.test(this.email)) {
        this.yonghuemail = ""
        return;
      }
      if (this.email == "") {
        (this.yonghuemail = "")
        return;
      }
      if (/\s/.test(this.email)) {
        (this.yonghuemail = "")
        return;
      }
      if (this.email.length > 64) {
        this.yonghuemail = "64";
        return;
      }
      this.yonghuemail = ""
    },
    //
    jiancepassword() {

      if (/^.{8,16}$/.test(this.password)) {
        this.yonghupassword = ""
      } else if (this.password === "") {
        (this.yonghupassword = "")
      } else {
        (this.yonghupassword = "8~16");
      }
    },
    //
    jizhume() {
      this.ifjizhume = !this.ifjizhume
      if (this.ifjizhume) {
        this.$store.commit("ifjizhuwo")
      }
    },
    //
    xieyi() {
      this.$router.push({ name: "UserAgreement" })
    },
    //
    wnagijpassdorw() {
      alert("");
      alert("");
    },
    //
    lijizhuce() {
      this.$router.push({ name: "UserRegistration" });
    },
  },
};
</script>
<style scoped>
:root {
  --primary-color: #3498db;
  --primarys-hover: #358ac3;
  --border-color: #e0e0e0;
  --text-color: #333;
  --background-light: #f8f9fa;
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  --transition: all 0.3s ease;
}

.yonghumingcuowu {
  color: red;
}

.content-page {
  width: 100%;
  margin: 0 auto;
}

.register-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 16px;
  box-shadow: var(--card-shadow);
  overflow: hidden;
  width: 100%;
}

.page-header {
  background: var(--primary-color);
  color: white;
  text-align: center;
  padding: 30px;
}

.page-header h2 {
  font-size: 28px;
  margin-bottom: 10px;
  font-weight: 600;
}

.page-header p {
  font-size: 16px;
  opacity: 0.9;
}

.form-content {
  padding: 30px;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.form-col {
  flex: 1;
  min-width: 250px;
}

.form-group {
  margin-bottom: 5px;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 15px;
  color: #555;
}

.form-control {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  font-size: 15px;
  transition: var(--transition);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}

.verification-group {
  display: flex;
  gap: 10px;
}

.verification-group .form-control {
  flex: 1;
}

.verification-code-btn {
  background-color: #f8f9fa;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 0 15px;
  cursor: pointer;
  color: var(--primary-color);
  font-weight: 500;
  white-space: nowrap;
  transition: var(--transition);
}

.verification-code-btn:hover {
  background-color: #e3f2fd;
}

.agreement-group {
  margin: 13px 0;
  display: flex;
  align-items: flex-start;
}

.agreement-group input {
  margin-top: 3px;
  margin-right: 8px;
}

.agreement-group a {
  color: var(--primary-color);
  text-decoration: none;
}

.agreement-group a:hover {
  text-decoration: underline;
}

.btn {
  display: block;
  width: 100%;
  padding: 13px 20px;
  background-color: var(--primary-color);
  color: white;
  font-size: 16px;
  font-weight: 500;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: var(--transition);
}

.btn:hover {
  background-color: rgb(30, 136, 229, 0.7);
  transform: translateY(-2px);
}

.btn:active {
  transform: translateY(0);
}

.login-link {
  text-align: center;
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
  font-size: 15px;
}

.login-link a {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  margin-left: 5px;
}

#goToRegister:hover {
  cursor: pointer;
}
</style>